import { useEffect } from "react";
import useCounter from "../hooks/useCounter";
import { useCart } from "../context/cart.context";

const ProdutoCard = ({ src, productName, description, alt, price, prodId }) => {
  const [counter, { increment, decrement, setCounter }] = useCounter();
  const {
    setParcial,
    setProductWithPrice,
    setTotal,
    parcial,
    productWithPrice,
  } = useCart();

  useEffect(() => {
    function totalize() {
      setTotal(parcial.reduce((total, amount) => total + amount, 0));
    }
    totalize();
  }, [parcial, setTotal]);
  const handleClick = () => {
    const existingProductIndex = productWithPrice.findIndex(
      (prod) => prod.productName === productName
    );

    if (existingProductIndex !== -1) {
      // Product is already in the cart, update the quantity
      setProductWithPrice((prevProduct) => {
        const updatedProducts = [...prevProduct];
        updatedProducts[existingProductIndex] = {
          ...updatedProducts[existingProductIndex],
          counter: updatedProducts[existingProductIndex].counter + counter,
          totalPrice:
            (updatedProducts[existingProductIndex].counter + counter) * price,
        };
        return updatedProducts;
      });
    } else {
      // Product is not in the cart, add it as a new item
      setProductWithPrice((prevProduct) => [
        ...prevProduct,
        {
          prodId: prodId,
          src: src,
          counter: counter,
          productName: productName,
          price: price,
          totalPrice: price * counter,
        },
      ]);
    }

    // Add the total price to the partial total
    setParcial((prevParcial) => [...prevParcial, price * counter]);

    // Reset the counter to 0
    setCounter(0);
  };

  return (
    <div className="card my-2 shadow" style={{ width: "13rem" }}>
      <img src={src} className="card-img-top my-2 px-4" alt={alt} />
      <div className="card-body">
        <h6 className="card-title">{productName}</h6>
        <p className="card-text">{description}</p>
        <p className="card-text">${price}</p>
        <div className="text-center ">
          <button onClick={decrement} className="btn btn-l mx-4">
            -
          </button>
          <span>{counter}</span>
          <button onClick={increment} className="btn btn-m mx-4">
            +
          </button>
          <button
            disabled={counter === 0 ? true : false}
            onClick={handleClick}
            className="btn btn-info btn-s mt-3 mx-auto"
          >
            Add to cart
          </button>
        </div>
      </div>
    </div>
  );
};

export default ProdutoCard;
