import { Link } from "react-router-dom";

const Footer = () => {
  return (
    <div className="d-flex flex-column align-items-center gap-2 bg-dark text-light p-3 ">
      <div className="d-flex">
        <Link
          style={{ color: "white" }}
          to={"https://github.com/vitorreisler"}
          target="_blank"
        >
          <i className="bi bi-github p-1" role="button" />
        </Link>
        <Link
          style={{ color: "white" }}
          to={"https://www.linkedin.com/in/vitorreisler/?locale=en_US"}
          target="_blank"
        >
          <i className="bi bi-linkedin p-1" role="button" />
        </Link>
      </div>
      <span className="">
        Created By Vitor Reisler {new Date().getFullYear()}
      </span>
    </div>
  );
};

export default Footer;
