import { NavLink, Link, useNavigate } from "react-router-dom";
import { Fragment } from "react";
import { useCart } from "../context/cart.context";

const Navbar = () => {
  const { total, productWithPrice } = useCart();
  const navigate = useNavigate();

  return (
    <nav
      className="navbar navbar-expand-sm navbar-dark bg-dark"
      aria-label="Third navbar example"
    >
      <div className="container-fluid">
        <NavLink
          to={"/loja-virtual-exemplo2"}
          className="navbar-brand"
          href="..."
        >
          <span>
            Exemplo <i className="bi bi-suit-spade-fill"></i> Bla
          </span>
        </NavLink>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarsExample03"
          aria-controls="navbarsExample03"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarsExample03">
          <ul className="navbar-nav me-auto mb-2 mb-sm-0">
            <li className="nav-item">
              <NavLink
                to={"/loja-virtual-exemplo2"}
                className="nav-link"
                aria-current="page"
                href="..."
              >
                Home
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink
                to={"/about"}
                className="nav-link"
                aria-current="page"
                href="..."
              >
                About
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink to={"/products"} className="nav-link">
                Products
              </NavLink>
            </li>

            <li className="nav-item dropdown">
              <NavLink
                className="nav-link dropdown-toggle"
                role="button"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                Categories
              </NavLink>
              <ul className="dropdown-menu">
                <li>
                  <Link to={"/products/shirts"} className="dropdown-item">
                    Shirts
                  </Link>
                </li>

                <li>
                  <Link to={"/products/pants"} className="dropdown-item">
                    Pants
                  </Link>
                </li>
                <li>
                  <Link to={"/products/shoes"} className="dropdown-item">
                    Shoes
                  </Link>
                </li>
              </ul>
            </li>
          </ul>
          <ul className="d-flex mb-0 list-unstyled ">
            <li className="nav-item dropdown">
              <NavLink
                className="nav-link dropdown-toggle"
                role="button"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                <i
                  role="button"
                  className="bi bi-cart-fill bg-light p-2 rounded"
                />
              </NavLink>
              <ul className="dropdown-menu dropdown-menu-car text-center  p-1 ">
                {productWithPrice.map((item) => (
                  <Fragment key={item.prodId}>
                    <div className="d-flex">
                      <img className="car-img my-2" src={item.src} alt="" />
                      <div className=" flex-column">
                        <li>Item: {item.productName} </li>
                        <li>Qtty: {item.counter}</li>
                        <li>Price: ${item.price}</li>
                        {/*<button className="btn btn-danger">
                        <i className="bi bi-trash"></i>
                      </button>*/}
                        <hr />
                      </div>
                    </div>
                  </Fragment>
                ))}
                <span className="px-5"> Total: ${total} </span>
                {total ? (
                  <button
                    onClick={() => navigate("/check-out")}
                    className="btn btn-success my-1"
                  >
                    CheckOut
                  </button>
                ) : (
                  ""
                )}
              </ul>
            </li>
            <li className="px-5 text-light"> Total: ${total} </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
