
const ImgWithText = ({ img, titleSection,contentSection }) => {
  return (
    <section className="container d-flex align-self-start flex-wrap my-3 border border-danger rounded ">
        {titleSection && <h4 className="my-2 text-light">{titleSection}</h4>}
      <div className="container d-flex align-self-start flex-wrap  ">
        <img className="rounded mt-2 mb-3 mx-auto common-img" src={img} alt="" />
        <div className=" d-flex flex-column align-self-center mx-auto container-p-homepg">
          <p className="text-light">
            {contentSection}
          </p>
          <button className="btn btn-outline-light btn-more-homepg my-2">More..</button>
        </div>
      </div>
    </section>
  );
};

export default ImgWithText;
