import { useProducts } from "../context/products.context";
import { Link } from "react-router-dom";

const ProductCategories = () => {
  const { pants, shirts, shoes } = useProducts();
  return (
    <div className="container container-categories my-3 d-flex flex-column border border-secundary rounded shadow ">
      <h2 className="ms-3">Categories</h2>
      <div className="d-flex justify-content-evenly flex-wrap gap-2  my-3">
        <Link className="text-decoration-none" to={"/products/pants"}>
          <div className="card card-category" style={{ width: "10rem" }}>
            <img
              src={pants[0].productImg}
              className="card-img-top"
              alt={pants[0].productIAlt}
            />
            <div className="card-body p-1">
              <h5 className="card-title">Pants</h5>
            </div>
          </div>
        </Link>
        <Link className="text-decoration-none" to={"/products/shirts"}>
          <div className="card card-category" style={{ width: "9rem" }}>
            <img
              src={shirts[0].productImg}
              className="card-img-top"
              alt={shirts[0].productIAlt}
            />
            <div className="card-body p-0">
              <h5 className="card-title ">Shirts</h5>
            </div>
          </div>
        </Link>
        <Link className="text-decoration-none" to={"/products/shoes"}>
          <div
            className="card card-category"
            role="button"
            style={{ width: "10rem" }}
          >
            <img
              src={shoes[0].productImg}
              className="card-img-top"
              alt={shoes[0].productIAlt}
            />
            <div className="card-body p-1">
              <h5 className="card-title">Shoes</h5>
            </div>
          </div>
        </Link>
      </div>
    </div>
  );
};

export default ProductCategories;
