import { useProducts } from "../context/products.context";
import ProdutoCard from "./prodCard";

const Shirts = ({ sectionTitle }) => {
  const { shirts } = useProducts();
  return (
    <>
      <h1 className="text-center">{sectionTitle || "Our T-shirts"}</h1>
      <div className="prodtuos d-flex flex-wrap justify-content-evenly">
        {shirts.map((item) => {
          return (
            <ProdutoCard
              key={item.productId}
              src={item.productImg}
              alt={item.productAlt}
              productName={item.productName}
              description={item.productDescription}
              price={item.productPrice}
              prodId={item.productId}
            />
          );
        })}
      </div>
    </>
  );
};

export default Shirts;
