import { useProducts } from "../context/products.context";
import ProdutoCard from "./prodCard";

const BestSellers = () => {
  const { pants, shirts, shoes } = useProducts();
  const bestSellerPants = [pants[0], pants[2], pants[3]];
  const bestSellerShirts = [shirts[0], shirts[2], shirts[4]];
  const bestSellerShoes = [shoes[0], shoes[2], shoes[4]];
  return (
    <div className="container container-bestseller my-3 d-flex flex-column border border-secundary rounded shadow ">
      <h2 className="ms-3">Best Sellers</h2>
      <div className="d-flex justify-content-center flex-wrap gap-2 mx-auto">
        {bestSellerPants.map((item) => {
          return (
            <ProdutoCard
              key={item.productId}
              productName={item.productName}
              src={item.productImg}
              alt={item.productAlt}
              description={item.productDescription}
              price={item.productPrice}
              prodId={item.productId}
            />
          );
        })}
        {bestSellerShoes.map((item) => {
          return (
            <ProdutoCard
              key={item.productId}
              productName={item.productName}
              src={item.productImg}
              alt={item.productAlt}
              description={item.productDescription}
              price={item.productPrice}
              prodId={item.productId}
            />
          );
        })}
        {bestSellerShirts.map((item) => {
          return (
            <ProdutoCard
              key={item.productId}
              productName={item.productName}
              src={item.productImg}
              alt={item.productAlt}
              description={item.productDescription}
              price={item.productPrice}
              prodId={item.productId}
            />
          );
        })}
      </div>
    </div>
  );
};

export default BestSellers;
